.cart-page {
    margin-top: 70px;
}
  .carts{
    display: flex;
    justify-content: space-evenly;
  }
  .cart-page h1 {
    padding: 15px !important;
    font-family: "roboto", sans-serif;
    font-weight: normal;
    background-color: rgba(0, 0, 255, 0.072) !important;
  }
  .cart-page h1 > p {
    font-size: 20px;
    margin-top: 10px;
  }
  
  .cart-page .card {
    margin-top: 20px;
    width: 100%;
    padding: 5px !important;
    height: 180px !important;
    margin-bottom: 25px !important;
    border: 2px outset;
    outline: 3px inset;
  }
 .remove {
  position:relative;
  bottom:-25px;
right: 50px;  
}
  .cart-page .cart-summary {
    text-align: center;
  }