.banner-img {
  margin-top: 60px;
}

.home-page h1 {
  color: #000;
  font-family: "Playfair Display", serif;
}

.cards{
  border: 3px outset ;
}

.all{
  margin-top: 80px;
}

.home-page {
  display: flex;
  flex-wrap: wrap;
}
.home-page .card {
  background-color: rgba(128, 128, 128, 0.097);
  width: 15rem;
}
.card-name-price {
  display: flex;
  justify-content:space-around;
}

.homebutton{
 display: flex;
 justify-content: space-between;
  position: relative;
  right: 100px;
}
.home-page .card-price {
  color: green;
  font-weight: bold;
}

.home-page .card-text {
  color: rgb(90, 89, 89);
}

.home-page .card img:hover {
  transform: scale(0.9);
}

.home-page .card-img-top {
  object-fit: contain;
  height: 250px !important;
}

/* filters */

.filters{
  margin-top: 100px;
  border: 4px outset;
  height: 110vh;
  left:20px;
  width: 20%;
}

.filters h4 {
  margin-top: 10px;
 text-align: center;
  color: black !important;
  border-bottom: 1px solid rgba(128, 128, 128, 0.337);
}

.ant-checkbox-wrapper:first-of-type {
  margin-left: 8px;
}

.ant-checkbox-wrapper {
  margin: 2px;
}
.ant-radio-wrapper {
  margin-bottom: 15px !important;
  margin-left: 10px;
}

@media screen and (min-width:769px) and (max-width:1024px){
  .filters{
    width: 25%;
  }
 
  .loadbutton{
    left: 20px;
  }
}

@media screen and (min-width:600px) and (max-width:768px){
  .filters{
    width: 30%;
  }

  .loadbutton{
    left: 20px;
  }
}

@media screen and (min-width:100px) and (max-width:645px){

   .filters{
    width: 100%;
  }

  .loadbutton{
    left: 20px;
  }
}

