.categoryproduct {
    margin-top: 100px !important;
    margin-left: 90px;
  }
  .categoryproduct .card {
    background-color: rgba(128, 128, 128, 0.097);
    border: 4px outset;
    width: 16rem;
    
  }
  .categoryproduct .card-name-price {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  
  .categoryproduct .card-price {
    color: green;
    font-weight: bold;
   
  }
  .categoryproduct button {
  left: 0px;
  }
  
  .categoryproduct .card-text {
    color: rgb(90, 89, 89);
  }
  
  .categoryproduct .card img:hover {
    transform: scale(0.9);
  }
  
  .categoryproduct .card-img-top {
    height: 300px !important;
  }