@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navbar {
  font-family: "Poppins", sans-serif;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
  --webkit-box-shadow: 0 8px 6px -6px gray;
  border-bottom: solid gray !important;
}

.nav-link {
  font-weight: 300 !important;
}

.active {
  border-bottom: 2px solid black;
}

.navbar-brand {
  font-weight: 700;
  font-family: "roboto", sans-serif;
  letter-spacing: 3px;
}

.search-form {
  margin-right: 200px;
  margin-top: 4px;
}
.search-form input {
  border: 2px inset;
  border-radius: 5px;
}

.dashboard{
  width: 50%;
  border: 7px outset;
  outline: 4px solid ;
}


.dashboards{
  margin-top: 20px;
  width: 30%;
  border: 4px outset;
  outline: 4px solid ;
}

.dash{
  margin-top: 60px;
  width: 28%;
  outline: 2px outset;
}

.createbutton{
  position: relative;
  left: -50px;
}

.modals{ 
border:5px solid;
margin-top: 90px;
width: 900px;
height: 20px;
margin-right: 180px
}

.searchbutton{
  margin-left:-50px;
  margin-right:100px;
}

.categorycard{
  border: 5px outset;
  outline: 2px outset;
}

.imgproduct{
  height: 300px;
  width: 200px;
}

.namecard{
color: black;
}



